<template>
  <v-app>
    <v-app-bar app color="light-blue darken-4" dark v-if="isHome" extended>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"> </v-app-bar-nav-icon>
      <v-toolbar-title to="/"> {{ appName }} </v-toolbar-title>

      <!-- Pemisah konten -->
      <v-spacer></v-spacer>

      <v-btn icon @click="setDialogComponent('cart')">
        <v-badge color="orange" overlap>
          <template v-slot:badge v-if="countCart > 0">
            <span>{{ countCart }}</span>
          </template>
          <v-icon>mdi-cart</v-icon>
        </v-badge>
      </v-btn>

      <v-text-field
        slot="extension"
        hide-details
        flat
        label="Search"
        prepend-inner-icon="mdi-magnify"
        solo-inverted
        @click="setDialogComponent('search')"
      ></v-text-field>
    </v-app-bar>

    <v-app-bar app color="light-blue darken-4" dark v-else>
      <v-btn icon @click.stop="$router.go(-1)">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>
      <v-spacer></v-spacer>

      <v-btn icon @click="setDialogComponent('cart')">
        <v-badge color="orange" overlap>
          <template v-slot:badge v-if="countCart > 0">
            <span>{{ countCart }}</span>
          </template>
          <v-icon>mdi-cart</v-icon>
        </v-badge>
      </v-btn>
    </v-app-bar>

    <!-- Sidebar -->
    <v-card>
      <v-navigation-drawer app v-model="drawer">
        <v-list>
          <v-list-item class="pa-2" v-if="!guest">
            <v-list-item-avatar>
              <v-img :src="getImage('/users/' + user.avatar)"></v-img>
            </v-list-item-avatar>

            <v-list-item-content>
              <v-list-item-title>{{ user.name }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>

          <div class="pa-2" v-if="guest">
            <v-btn
              block
              color="blue white--text"
              class="mb-1"
              @click="setDialogComponent('login')"
            >
              <v-icon left>mdi-lock</v-icon>
              Login
            </v-btn>
            <v-btn
              block
              color="yellow accent-4"
              class="mb-1"
              @click="setDialogComponent('register')"
            >
              <v-icon left>mdi-account</v-icon>
              Register
            </v-btn>
          </div>

          <v-divider></v-divider>
          <template v-for="(item, index) in menus">
            <v-list-item
              :key="`menu-` + index"
              :to="item.route"
              v-if="!item.auth || (item.auth && !guest)"
            >
              <v-list-item-icon>
                <v-icon left>{{ item.icon }}</v-icon>
              </v-list-item-icon>

              <v-list-item-content>
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </template>
        </v-list>

        <!-- Logout -->
        <template v-slot:append v-if="!guest">
          <div class="pa-2">
            <v-btn block color="blue-grey" dark @click="logout">
              <v-icon left>mdi-lock</v-icon>
              Logout
            </v-btn>
          </div>
        </template>
      </v-navigation-drawer>
    </v-card>

    <!-- Ini Alert -->
    <alert />

    <!-- Ini Dialog -->
    <keep-alive>
      <v-dialog
        v-model="dialog"
        fullscreen
        hide-overlay
        transition="scale-transition"
      >
        <component :is="currentComponent" @closed="setDialogStatus"></component>
      </v-dialog>
    </keep-alive>

    <!-- v-main adalah v-content (deprecated) -->
    <v-main class="grey lighten-4">
      <!-- Jika menggunakan vue-router -->
      <v-slide-y-reverse-transition>
        <router-view></router-view>
      </v-slide-y-reverse-transition>
    </v-main>

    <v-card>
      <v-footer app class="text-center grey darken-4 white--text">
        <v-card-text>
          &copy; {{ new Date().getFullYear() }} - <strong>VueShop Books</strong>
        </v-card-text>
      </v-footer>
    </v-card>
  </v-app>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "App",

  components: {
    Alert: () => import("/src/components/Alert.vue"),
    Search: () => import("/src/components/Search.vue"),
    Login: () => import("/src/components/Login.vue"),
    Register: () => import("/src/components/Register.vue"),
    Cart: () => import("/src/components/Cart.vue"),
  },

  data: () => ({
    drawer: false,
    menus: [
      { title: "Home", icon: "mdi-home", route: "/" },
      { title: "Profile", icon: "mdi-account", route: "/profile", auth: true },
      {
        title: "My Order",
        icon: "mdi-shopping",
        route: "/my-order",
        auth: true,
      },
      { title: "About", icon: "mdi-help-box", route: "/about" },
    ],
  }),
  methods: {
    ...mapActions({
      setDialogStatus: "dialog/setStatus",
      setDialogComponent: "dialog/setComponent",
      setAuth: "auth/set",
      setAlert: "alert/set",
    }),

    logout() {
      let config = {
        headers: {
          Authorization: "Bearer " + this.user.api_token,
        },
      };

      this.axios
        .post("/logout", {}, config)
        .then(() => {
          this.setAuth({});
          this.setAlert({
            status: true,
            color: "success",
            text: "Logout Success",
          });
        })
        .catch((error) => {
          let { data } = error.response;
          this.setAlert({
            status: true,
            color: "error",
            text: data.message,
          });
        });
    },
  },
  computed: {
    isHome() {
      return this.$route.path === "/";
    },
    ...mapGetters({
      countCart: "cart/count",
      guest: "auth/guest",
      user: "auth/user",
      dialogStatus: "dialog/status",
      currentComponent: "dialog/component",
    }),
    dialog: {
      get() {
        return this.dialogStatus;
      },
      set(value) {
        this.setDialogStatus(value);
      },
    },
  },
};
</script>
