<template>
  <div>
    <!-- Template Category -->
    <v-container class="ma-0 pa-2 mt-2" grid-list-sm grid-list-lg>
      <div class="text-right">
        <v-btn small text to="/categories" class="blue--text">
          All Categories <v-icon>mdi-chevron-double-down-right</v-icon>
        </v-btn>
      </div>
      <v-layout wrap>
        <v-flex
          v-for="category in categories"
          :key="`category-` + category.id"
          xs6
          lg3
        >
          <v-card :to="`/category/` + category.slug">
            <v-img
              :src="getImage('/categories/' + category.image)"
              class="white--text"
            >
              <v-card-title
                class="fill-height align-end"
                v-text="category.name"
              >
              </v-card-title>
            </v-img>
          </v-card>
        </v-flex>
      </v-layout>

      <div class="text-right">
        <v-btn small text to="/books" class="blue--text">
          All Books <v-icon>mdi-chevron-right</v-icon>
        </v-btn>
      </div>
      <v-layout wrap>
        <v-flex v-for="book in books" :key="`book-` + book.id" xs6 lg3>
          <book-item :book="book" />
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  name: "Home",
  data: () => ({
    categories: [],
    books: [],
  }),

  components: {
    BookItem: () => import("@/components/BookItem.vue"),
  },

  methods: {},

  created() {
    console.log("get data categories");
    this.axios
      .get("/categories/random/4")
      .then((response) => {
        let { data } = response.data;
        this.categories = data;
        // console.log(data);
      })
      .catch((err) => {
        let { response } = err;
        console.log(response);
      });

    this.axios
      .get("/books/top/12")
      .then((response) => {
        let { data } = response.data;
        // console.log(data);
        this.books = data;
      })
      .catch((error) => {
        let { ressError } = error;
        console.log(ressError);
      });
  },
};
</script>
